import {Box, Button, Heading, Link, Text} from '@primer/react'
import {useEffect, useState} from 'react'

import type {DeleteConfigurationItemProps} from './DeleteConfigurationListItem'
import {DeleteConfigurationListItem} from './DeleteConfigurationListItem'

interface DeleteConfigurationDialogProps {
  name: string
  instances: DeleteConfigurationItemProps[]
  dialogId: string
  csrf: string
  form_submit_path: string
  read_only_user: boolean
  docs_link: string
}

interface DisplayConfigurationsListOrEmptyStateProps {
  instances: DeleteConfigurationItemProps[]
  readOnlyUser: boolean
  changeOutdated: (category: string) => () => void
}

const DisplayConfigurationsListOrEmptyState = ({
  instances,
  readOnlyUser,
  changeOutdated,
}: DisplayConfigurationsListOrEmptyStateProps) => {
  const showTool = new Set(instances.map(instance => instance.tool_name)).size > 1
  const newInstances = instances
    .filter(branch => !branch.is_outdated)
    .map(branch => {
      return (
        <DeleteConfigurationListItem
          key={branch.category}
          is_fixed={branch.is_fixed}
          is_outdated={branch.is_outdated}
          category={branch.category}
          created_at={branch.created_at}
          tool_name={branch.tool_name}
          readOnly={readOnlyUser}
          toggleAction={changeOutdated(branch.category)}
          showTool={showTool}
        />
      )
    })
  if (newInstances.length === 0) {
    return (
      <Box className="Overlay-body" sx={{pt: 3, maxHeight: '25vh'}}>
        <Box sx={{display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center'}}>
          <Heading sx={{fontSize: 16, fontWeight: 600}} as="h4">
            All configurations deleted
          </Heading>
          <Text sx={{color: 'fg.muted'}}>Deleting all configurations will close this alert as fixed.</Text>
        </Box>
      </Box>
    )
  }
  return (
    <Box className="Overlay-body" sx={{pt: 3, maxHeight: '25vh'}}>
      {newInstances}
    </Box>
  )
}

function getChangedInstances(instances: DeleteConfigurationItemProps[], props: DeleteConfigurationDialogProps) {
  return instances.filter((instance, index) => {
    return instance.is_outdated !== props.instances[index]!.is_outdated
  })
}

export const DeleteConfigurationDialog = (props: DeleteConfigurationDialogProps) => {
  const read_only = props.read_only_user
  const [instances, setInstances] = useState(props.instances)
  const changeOutdated = (category: string) => {
    const anon = () => {
      const newInstances = instances.map(instance => {
        if (instance.category === category) {
          instance = {...instance, is_outdated: !instance.is_outdated}
        }
        return instance
      })
      setInstances(newInstances)
    }
    return anon
  }
  const onSubmit = async () => {
    const changedInstances = instances.filter((instance, index) => {
      return instance.is_outdated !== props.instances[index]!.is_outdated
    })

    const toBeMarkedOutdated = changedInstances
      .filter(instance => instance.is_outdated)
      .map(instance => {
        return {
          branch_name: props.name,
          category: instance.category,
          tool_name: instance.tool_name,
        }
      })

    await fetch(props.form_submit_path, {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
        'Scoped-CSRF-Token': props.csrf,
        'X-Requested-With': 'XMLHttpRequest',
        'GitHub-Verified-Fetch': 'true',
      },
      body: JSON.stringify({
        mark_as_outdated: toBeMarkedOutdated,
      }),
    })
    window.location.reload()
  }

  useEffect(() => {
    const onDialogClose = () => {
      setInstances(props.instances)
    }

    const el = document.getElementById(props.dialogId)
    if (el && el.parentElement) {
      const mutationObserver = new MutationObserver(onDialogClose)
      mutationObserver.observe(el.parentElement, {attributes: true})
      return () => {
        mutationObserver.disconnect()
      }
    }
  }, [props.dialogId, props.instances])

  return (
    <div>
      <DisplayConfigurationsListOrEmptyState
        instances={instances}
        changeOutdated={changeOutdated}
        readOnlyUser={read_only}
      />
      <Box
        className="Overlay-footer Overlay-footer--divided"
        sx={{
          p: 3,
          display: 'flex',
          alignItems: 'flex-end',
          gap: 'var(--stack-gap-condensed, 8px)',
          flexWrap: 'nowrap',
        }}
      >
        <Box sx={{flexGrow: 1}}>
          {getChangedInstances(instances, props).length > 0 && (
            <Text as="p" sx={{color: 'fg.subtle'}}>
              <Link href={props.docs_link} inline>
                Deleting a configuration
              </Link>
              &nbsp;may have an impact on the status of other alerts on the same branch.
            </Text>
          )}
        </Box>
        <Button data-close-dialog-id={props.dialogId} sx={{mr: 1}}>
          Cancel
        </Button>
        <Button variant="primary" onClick={onSubmit} disabled={read_only}>
          Save changes
        </Button>
      </Box>
    </div>
  )
}

try{ DisplayConfigurationsListOrEmptyState.displayName ||= 'DisplayConfigurationsListOrEmptyState' } catch {}
try{ DeleteConfigurationDialog.displayName ||= 'DeleteConfigurationDialog' } catch {}