import type {Icon} from '@primer/octicons-react'
import {ShieldCheckIcon, ShieldIcon, TrashIcon} from '@primer/octicons-react'
import {Box, Heading, IconButton, Octicon, RelativeTime, Text, Tooltip} from '@primer/react'

interface DeleteConfigurationButtonProps {
  defaultIcon: Icon
  ariaLabel: string
  readOnly: boolean
  toggleAction: () => void
}

const DeleteConfigurationButton = (props: DeleteConfigurationButtonProps) => {
  const onClick = () => {
    props.toggleAction()
  }
  return (
    <Tooltip aria-label={props.ariaLabel} direction="sw" noDelay>
      {/* eslint-disable-next-line primer-react/a11y-remove-disable-tooltip */}
      <IconButton
        unsafeDisableTooltip={true}
        icon={props.defaultIcon}
        variant="invisible"
        aria-label={props.ariaLabel}
        onClick={onClick}
        disabled={props.readOnly}
      />
    </Tooltip>
  )
}

export interface DeleteConfigurationItemProps {
  // these props are snake case because they are coming directly from rails
  is_fixed: boolean
  is_outdated: boolean
  category: string
  created_at: string
  tool_name: string
  readOnly: boolean
  toggleAction: () => void
  showTool?: boolean
}

export const DeleteConfigurationListItem = (props: DeleteConfigurationItemProps) => {
  const icon = (isFixed: boolean) => {
    return isFixed ? ShieldCheckIcon : ShieldIcon
  }
  const color = (isFixed: boolean) => {
    return isFixed ? 'done.fg' : 'success.fg'
  }
  return (
    <Box sx={{display: 'flex', flexGrow: 0, flexDirection: 'row'}}>
      <Octicon icon={icon(props.is_fixed)} sx={{color: color(props.is_fixed)}} />
      <Box sx={{flexGrow: 2, flexDirection: 'column', ml: 1, mb: 1, mr: 1, mt: 0}}>
        <Heading as="h5" sx={{fontSize: '14px'}}>
          {props.category || 'API Upload'} {props.showTool && `(${props.tool_name})`}
        </Heading>
        <Text as="p" sx={{color: 'fg.subtle'}}>
          Last updated <RelativeTime datetime={props.created_at} tense="past" />
        </Text>
      </Box>
      <div>
        <DeleteConfigurationButton
          defaultIcon={TrashIcon}
          ariaLabel={'Delete configuration'}
          toggleAction={props.toggleAction}
          readOnly={props.readOnly}
        />
      </div>
    </Box>
  )
}

try{ DeleteConfigurationButton.displayName ||= 'DeleteConfigurationButton' } catch {}
try{ DeleteConfigurationListItem.displayName ||= 'DeleteConfigurationListItem' } catch {}